import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { DataService } from './data.service';

@Injectable()
export class LoginService {
  private regUrl = 'http://localhost:1234/api/';
  constructor(private http: HttpClient) { }
  getUserData(){
    return this.http.get<any>(`${this.regUrl}stats`);
  }
  register(user: any){
    return this.http.post<any>(`${this.regUrl}create`, user);
  }
  datareg(data: any){
    return this.http.patch<any>(`${this.regUrl}userdata`, data);
  }
  login(data: any){
    return this.http.post<any>(`${this.regUrl}login`, data);
  }
  forgot(data: any){
    return this.http.post<any>(`${this.regUrl}forgot`, data);
  }
  changePassword(data: any){
    return this.http.patch<any>(`${this.regUrl}change`, data);
  }
  showpage(){
    if (sessionStorage.getItem('token') || sessionStorage.getItem('GuestData')){
      return true;
    }
    else{
      return false;
    }
  }
}
