import { Component, OnInit } from '@angular/core';
import { LoginService } from 'src/app/services/login.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-user-data',
  templateUrl: './user-data.component.html',
  styleUrls: ['./user-data.component.scss']
})
export class UserDataComponent implements OnInit {
  userDataForm = {
    height: null,
    weight: null,
    waist: null,
    neck: null,
    hip: null,
    ankle: null,
    wrist: null,
    bType: {
      ecto: null,
      meso: null,
      endo: null
    },
    bt: {
      q1: null,
      q2: null,
      q3: null,
      q4: null,
      q5: null,
      q6: null,
      q7: null,
      q8: null
    }
  };
  isGuest: boolean;
  message: string;
  guest = {age: null, gender: ''};
  constructor(
    private lgn: LoginService,
    private router: Router) {

   }

  ngOnInit(): void {
    if (sessionStorage.getItem('token')){
      this.isGuest = false;
    }
    else{
      this.isGuest = true;
    }
  }
  calc_bt(){
    let ecto = 0;
    let meso = 0;
    let endo = 0;
    const values = Object.values(this.userDataForm.bt);
    values.forEach(element => {
      if (element === 'A'){
        ecto ++;
      }
      else if (element === 'B'){
        meso ++;
      }
      else if (element === 'C'){
        endo ++;
      }
    });
    this.userDataForm.bType.ecto = ecto / 8 * 100;
    this.userDataForm.bType.meso = meso / 8 * 100;
    this.userDataForm.bType.endo = endo / 8 * 100;
  }
  addData(){
    if (!sessionStorage.getItem('token')){
      this.calc_bt();
      sessionStorage.setItem('GuestData', JSON.stringify( Object.assign(this.guest, {data: this.userDataForm}) ));
      this.router.navigate([`/stats`]);
    }
    else{
      this.calc_bt();
      delete this.userDataForm.bt;
      this.lgn.datareg(this.userDataForm)
        .subscribe(
          res => {
            if (res.message === 'success'){
              this.router.navigate([`/stats`]);
            }
          },
          err => {
            this.message = 'Opps! Something went wrong!';
          }
        );
        }
  }
}
