import { Component, OnInit } from '@angular/core';
import { DataService } from 'src/app/services/data.service';

@Component({
  selector: 'app-full-data',
  templateUrl: './full-data.component.html',
  styleUrls: ['./full-data.component.scss']
})
export class FullDataComponent implements OnInit {
  user: any;
  phy: string;
  constructor(private dataService: DataService) {
    this.dataService.currentMessage.subscribe(
      data => {
        if (data){

          this.user = data;
          this.phy = this.calc_physi(this.user.bfp, this.user.gender);

        }
      }
    );
  }
  ngOnInit(): void {
  }
  calc_physi(bfp: number, g: string){
    if (bfp && g){
      if (g === 'M'){
        if      ( bfp >= 2 && bfp < 6 ) { return 'Essestial Fat'; }
        else if ( bfp >= 6 && bfp < 14 ) { return 'Athlete'; }
        else if ( bfp >= 14 && bfp < 18 ) { return 'Fitness'; }
        else if ( bfp >= 18 && bfp < 25 ) { return 'Average'; }
        else if ( bfp >= 25) { return 'Obese'; }
      }
      else{
        if      ( bfp >= 10 && bfp < 14 ) { return 'Essestial Fat'; }
        else if ( bfp >= 14 && bfp < 21 ) { return 'Athlete'; }
        else if ( bfp >= 21 && bfp < 25 ) { return 'Fitness'; }
        else if ( bfp >= 25 && bfp < 32 ) { return 'Average'; }
        else if ( bfp >= 32) { return 'Obese'; }
      }
    }
    else{
      return 'Unknown';
    }
  }

}
