import { Component, OnInit } from '@angular/core';
import { LoginService } from '../../services/login.service';
import { Router } from '@angular/router';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { PasswordValidator } from 'src/app/shared/validator.pass';


@Component({
  selector: 'app-create',
  templateUrl: './create.component.html',
  styleUrls: ['./create.component.scss']
})
export class CreateComponent implements OnInit {
  serverMessage: string;
  signupForm: FormGroup;
  get name(){
    return this.signupForm.get('name');
  }
  get email(){
    return this.signupForm.get('email');
  }
  get pass0(){
    return this.signupForm.get('password0');
  }
  get pass(){
    return this.signupForm.get('password');
  }
  get gender(){
    return this.signupForm.get('gender');
  }
  get age(){
    return this.signupForm.get('age');
  }
  constructor(private lgn: LoginService,
              private route: Router,
              private fb: FormBuilder) {
   }
   aa(){
    console.log(this.signupForm);
  }
  ngOnInit(): void {
    this.signupForm = this.fb.group({
      name: ['', [Validators.required, Validators.pattern('[a-zA-Z][a-zA-Z ]+[a-zA-Z]$')]],
      email: ['', [Validators.required, Validators.pattern('[^@]+@[^@]+\.[a-zA-Z]{2,6}')]],
      password0: ['', [Validators.required, Validators.minLength(5)]],
      password: ['', [Validators.required, Validators.minLength(5)]],
      age: ['', [Validators.required]],
      gender: ['', [Validators.required]]
    }, {validator : PasswordValidator});
  }
  submit(){
    this.signupForm.markAllAsTouched();
    if (this.signupForm.valid)
    { delete this.signupForm.value.password0;
      this.lgn.register(this.signupForm.value)
       .subscribe(
         res => {
                  this.serverMessage = res.message;
                  sessionStorage.setItem('token', res.token);
                  sessionStorage.setItem('temp', JSON.stringify({
                                                                age: this.signupForm.get('age').value,
                                                                gender: this.signupForm.get('gender').value
                                                              }));
                  if (res.token){
                  this.route.navigateByUrl(`/userdata`);
                  }
                 },
         err => { }
       );
    }
  }

}
