import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';

import { CreateRoutingModule} from './create-router.module';
import { UserDataModule } from './user-data/user-data.module';


import { CreateComponent } from './create-user/create.component';

import { LoginService } from '../services/login.service';
import { CookieService } from 'ngx-cookie-service';
import { DataService } from '../services/data.service';

@NgModule({
  declarations: [ CreateComponent ],
  imports: [ CommonModule, FormsModule, ReactiveFormsModule, UserDataModule, CreateRoutingModule ],
  exports: [ CreateComponent ],
  providers: [ LoginService, DataService, CookieService ]
})
export class CreateModule { }
