import { Component, OnInit } from '@angular/core';
import { LoginService } from 'src/app/services/login.service';
import { ActivatedRoute } from '@angular/router';

import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { PasswordValidator } from 'src/app/shared/validator.pass';

@Component({
  selector: 'app-change-pass',
  templateUrl: './change-pass.component.html',
  styleUrls: ['./change-pass.component.scss']
})
export class ChangePassComponent implements OnInit {
  changeForm: FormGroup;
  message: string;
  password0: string;
  get pass(){
    return this.changeForm.get('password');
  }
  get pass0(){
    return this.changeForm.get('password0');
  }
  constructor(private lgn: LoginService,
              private route: ActivatedRoute,
              private fb: FormBuilder) {}

  ngOnInit(): void {
    this.changeForm = this.fb.group({
      password0: ['', [Validators.required, Validators.minLength(5)]],
      password: ['', [Validators.required, Validators.minLength(5)]]
    }, { validator: PasswordValidator });
  }
  submit() {
    this.changeForm.markAllAsTouched();
    if (this.changeForm.valid){
      sessionStorage.setItem('token', this.route.snapshot.queryParams.crypt);
      this.lgn.changePassword(this.changeForm.value)
          .subscribe(
            res => { if (res.message === 'err'){
                        this.message = res.message;
                      }
                    },
            err => {this.message = 'err'; }
            );
          }
  }
}
